import React, { Component }  from 'react';
import Logo from '../img/Logo.png';

class RunningInstance extends Component{
    
    render(){
        return(
            <div>
                 <div className="logoImg">
                    <img src={Logo} alt="logo" style={{height: '200px', display: 'inline-block'}}/>
                    <div  style={{display: 'inline-block', textAlign: 'center',fontSize: '35px', fontWeight:'bold', fontFamily:'Helvetica'}}>Running Instance</div>
                </div>

                <div className="video"
                    style={{
                        position: "relative",
                        paddingBottom: "56.25%" /* 16:9 */,
                        paddingTop: 50,
                        height: 0
                    }}>
                    <iframe 
                        style={{
                        position: "absolute",
                        top: 40,
                        left: 0,
                        width: "100%",
                        height: "85%"
                        
                        }}
                        src={`http://52.172.146.59:8080/com.zensar.zenlabs.zeva/`}
                        title="runningInstance"
                        frameBorder="0"/>
                 </div>
                
            </div>
           
        )
    }

}
export default RunningInstance;