import React, { Component } from 'react';
import Toolbar from '../Toolbar/Toolbar';
import RunningInstance from './RunningInstance';


class RunningInstanceLayout extends Component{
    render(){
      
        return (
            <div>
                 <header className="Layout-header"> 
                    <Toolbar/>
                </header>
                <main style={{marginLeft: '136px'}}>
                  <RunningInstance/>
                </main>
        </div>
        );
        }


}

export default RunningInstanceLayout;