import React, { Component }  from 'react';
import Logo from '../img/Logo.png';
import {Link} from "react-router-dom";


class ZenAnalyticaDashboardLayout extends Component{
    
    render(){
        return(
            <div>
                 <div className="logoImg">
                    <img src={Logo} alt="logo" style={{height: '200px', display: 'inline-block'}}/>
                    <div  style={{display: 'inline-block', textAlign: 'right',fontSize: '35px', fontWeight:'bold', fontFamily:'Helvetica'}}>ZenAnalytica ITSM Dashboard</div>
                </div>

                <div className="video"
                    style={{
                        position: "relative",
                        paddingBottom: "56.25%" /* 16:9 */,
                        paddingTop: 50,
                        height: 0
                    }}>
                    <iframe
                        style={{
                        position: "absolute",
                        top: 40,
                        left: 0,
                        width: "100%",
                        height: "85%"
                        }}
                        src={`https://demo.zenanalytica.com/ticketAnalyticsDescriptive.jsp;jsessionid=7F7A9D51AF925A0C15F89DAE0F63B07B`}
                        title="zenAnalytica"
                        frameBorder="0"/>
                 </div>
                 <div className="tabView" style={{alignContent:'center'}}>
                    <Link to="/DashboardLayout">  
                        <h4 style={{display: 'inline-block', textAlign: 'center',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingRight:'100px'}}>Save AND Proceed</h4>
                    </Link>
                    <Link to="/LibraryLayout">  
                        <h4 style={{display: 'inline-block', textAlign: 'right',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingLeft:'680px'}}>Decline</h4>
                    </Link>
                </div>
            </div>
           
        )
    }

}
export default ZenAnalyticaDashboardLayout;