import React, { Component } from 'react';
import Logo from '../img/Logo.png';
import { FormGroup,Form, Input}  from 'reactstrap';
import {Link} from "react-router-dom";


class Library extends Component{
    render(){
      
        return (
            <div>
                <div className="logoImg">
                <img src={Logo} alt="logo" style={{height: '150px', display: 'inline-block',paddingRight:"45rem"}}/>
                <div  style={{display: 'inline-block', textAlign: 'right',fontSize: '35px', fontWeight:'bold', fontFamily:'Helvetica',left:"40%"}}>Installation Guide</div>
            </div>
                <div>
                    <Form className="login-form" style={{textAlign:'right'}}>
                        <FormGroup>
                            <Input  type="textarea" rows="20" cols="10" name="text" 
                            value =" Stable — Ubuntu 16.04
                            1) 16 core preferred(Minimum 8 cores)
                            2) 32 gb RAM (Minimum 16 GB)
                            3) 1TB / 500 Gb HDD (As per requirement , 200Gb min.)"/>
                        </FormGroup>
                    </Form>
                </div>            
                <div className="tabView" style={{alignContent:'center'}}>
                    <Link to="/OnboardingChatBotLayout">  
                        <h4 style={{display: 'inline-block', textAlign: 'center',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingRight:'100px'}}>Accept and Proceed</h4>
                    </Link>
                    <Link to="/LibrayLayout">  
                        <h4 style={{display: 'inline-block', textAlign: 'right',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingLeft:'680px'}}>Decline</h4>
                    </Link>
                </div>
        </div>
        );
        }


}

export default Library;