import React, { Component } from 'react';
import './Toolbar.css';
import DashboardImg from '../img/Dashboard.png';
import LibraryImg from '../img/Library.png';
import NotificationImg from '../img/Notification.png';
import UserImg from '../img/User.png';
import ToggleButton from '../img/toggleButton.png';
import LogoutImg from '../img/Icon ionic-ios-exit.svg';
import {Link} from "react-router-dom";


class Toolbar extends Component{
    render(){
      
        return (
            <div>
                <div className="toolbar" style={{}}>
                    <nav className="toolbar_navigation">
                        <div className="toolbar_navigation_items">
                            <ul>
                                <br/>   
                                <Link to="/DashboardLayout">  
                                    <h6 style={{color: 'white'}}>
                                        <img src={ToggleButton} alt="ToggleButton" title="ToggleButton" style={{position: "absolute",top:"32px",height:"14px", width:"24px",float: "none",left:"30%"}}/>
                                    </h6>
                                </Link>  

                                <Link to="/DashboardLayout">  
                                    <h6 style={{color: 'white',position: "absolute",top:"102px",float: "none",left:"20%",textAlign: "center"}}>
                                        <img src={DashboardImg} alt="Dashboard" title="Dashboard" style={{ height:"27.29px", width:"40.6px"}}/>
                                        <br/><span style={{ fontSize:"10px",font: "Regular 16px/18px Helvetica",textAlign: "center"}}>Dashboard</span>
                                    </h6>
                                </Link>  
                              
                                <Link to="/LibraryLayout">  
                                    <h6 style={{color: 'white', position: "absolute",top:"172px",textAlign: "center",float: "none",left:"30%"}}>
                                        <img src={LibraryImg} alt="Library" title= "Library" style={{height:"32px", width:"32px"}}/>
                                        <br/><span style={{ fontSize:"10px",font: "Regular 16px/18px Helvetica",textAlign: "center"}}>Library</span>
                                     </h6>
                                </Link>  
                               
                                <Link to="/DashboardLayout">  
                                    <h6 style={{color: 'white', position: "absolute",top:"242px",textAlign: "center",float: "none",left:"20%"}}>
                                        <img src={NotificationImg} alt="Notification" title="Notification" style={{height:"31px", width:"26px"}}/>
                                        <br/><span style={{ fontSize:"10px",font: "Regular 16px/18px Helvetica",textAlign: "center"}}>Notification</span>
                                     </h6>
                                </Link>     
                                
                                <Link to="/DashboardLayout">  
                                    <h6 style={{color: 'white',position: "absolute",top:"472px",alignSelf:"center",float: "none",left:"15%"}}>
                                        <img src={UserImg} alt="User" title="User Profile" style={{height:"40px", width:"40px" }}/>
                                        <br/><span style={{ fontSize:"10px",font: "Regular 16px/18px Helvetica",textAlign:"center"}}>Admin</span>
                                     </h6>
                                </Link>   
                               
                                
                            </ul>
                        </div>
                    </nav>
                </div>
                <div className ="Logout">
                    <Link to="/Logout">  
                        <img src={LogoutImg} alt="logout" title="Logout" style={{ position: "absolute",height:"44px", width:"57px",alignSelf:"center"}} />
                    </Link> 
                </div>
            </div>
        );
        }


}

export default Toolbar;