import React, { Component } from 'react';
import Logo from '../img/Logo.png';
import ZEVA from '../img/ZEVA.png';
import ZenAnalytica_Logo from '../img/ZenAnalytica_Logo.png';
import './Library.css';
import {Link} from "react-router-dom";


class Library extends Component{
    render(){
      
        return (
            <div>
        <div className="logoImg">
                <img src={Logo} alt="logo" style={{height: '100px', display: 'inline-block',paddingRight:"45rem"}}/>
                <div  style={{display: 'inline-block', textAlign: 'right',fontSize: '30px', fontWeight:'bold', fontFamily:'Helvetica',left:"40%"}}>Service Library 
                        <p style={{textAlign: 'center',fontSize: '10px', fontFamily:'Helvetica', color:'#2D3642'}}>(Access all your services here)</p>
                    </div>
                </div>
                <br/>
            <br/>
            <div  style={{display: 'inline-block', textAlign: 'center',fontSize: '20px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5'}}> AL-Led Self Heal 
            </div>
            <br/>
            <div className="tabView" style={{alignContent:'center'}}>
            <div className="serviceImg" >
                <Link to="/InstallationGuidePage">  
                    <img src={ZEVA} alt="ZEVA" style={{height: '41px',width:'71px', textAlign:'center', top:'20px'}}/>
                </Link>
            
            </div>
            <br/>
            <br/>
            <br/>
            <br/>
            <div  style={{display: 'inline-block', textAlign: 'center',fontSize: '20px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5'}}> AI-Led Application Development
            </div>
            <div className="serviceImg">
                <Link to="/RecommConfigurationLayout">  
                    <img src={ZenAnalytica_Logo} alt="ZenAnalytica" style={{height: '41px',width:'81px', textAlign:'center', top:'20px', paddingTop:'1rem'}}/>
                </Link>
            </div>
          
            </div>
        </div>
        );
        }


}

export default Library;