import React, { Component } from 'react';
import Toolbar from '../Toolbar/Toolbar';
import ZenAnalyticaDashboard from './ZenAnalyticaDashboard';


class ZenAnalyticaDashboardLayout extends Component{
    render(){
      
        return (
            <div>
                 <header className="Layout-header"> 
                    <Toolbar/>
                </header>
                <main style={{marginLeft: '136px'}}>
                  <ZenAnalyticaDashboard/>
                </main>
        </div>
        );
        }


}

export default ZenAnalyticaDashboardLayout;