import React, { Component } from 'react';
import './Dashboard.css';
import Logo from '../img/Logo.png';
import NewInstances from './NewInstances/NewInstances';


class Dashboard extends Component{
    render(){
      
        return (
            <div>
            <div className="logoImg">
                <img src={Logo} alt="logo" style={{height: '100px', display: 'inline-block',paddingRight:"45rem"}}/>
                <div  style={{display: 'inline-block', textAlign: 'right',fontSize: '30px', fontWeight:'bold', fontFamily:'Helvetica',left:"40%"}}>Dashboard</div>
            </div>
            <br/>
            <div className="tabView" style={{textAlign:"center"}}>
                <div className="tab" style={{font:"Helvetica",fontWeight:'bold',fontSize: '45px'}}><button >Running Instances</button></div>
                <div className="vl"></div>
                <div className="tab" style={{font:"Helvetica",fontWeight:'bold',fontSize: '45px'}}><button  >New Instances</button></div>
            </div>
            <br/>
            <br/>
            <div><NewInstances/></div>
            </div>
        );
        }


}

export default Dashboard;