import React, { Component }  from 'react';
import {Link} from "react-router-dom";
import "./OnboardingChatBot.css";
import Logo from '../img/Logo.png';
import { Form, FormGroup, Input}  from 'reactstrap';



export default class OnBoardingService extends Component{
    state = {
        loading: false
    };
    fetchData = () =>{
        this.setState({loading:true})

        setTimeout(()=>{
            this.setState({loading: false});
        },2000)
        document.getElementById("errmsg").innerHTML="Deployment Successful. Click on 'Proceed' for configuration"

    };

    render(){

        const {loading} = this.state;

        return(
            <div>
            <div className="logoImg">
                <img src={Logo} alt="logo" style={{height: '150px', display: 'inline-block',paddingRight:"45rem"}}/>
                <div  style={{display: 'inline-block', textAlign: 'right',fontSize: '30px', fontWeight:'bold', fontFamily:'Helvetica',left:"40%"}}>OnBoarding Services</div>
            </div>
            <div>
                <Form className="deployment_path" >
                        <FormGroup class="form-group form-inline">
                            Deployment Path <Input type="text" placeholder = "Deployment Path" name="deployementPath" />
                        </FormGroup>
                    
                        <div style={{ marginTop: "60px" }}>
                            <button className="button" onClick={this.fetchData} disabled={loading}>
                                {loading && ( <i className="fa fa-refresh fa-spin" style={{ marginRight: "5px" }} /> )}
                                {loading && <span>Executing Scripts</span>}
                                {!loading && <span>Submit</span>}
                            </button>
                        </div>
                        
                    </Form>
                    <div className="text-center">
                        <div id="errmsg" className="errmsg"></div>
                    </div>
                    <div className="tabView" style={{alignContent:'center'}}>
                        <Link to="/ChatBotConfigurationLayout">  
                            <h4 style={{display: 'inline-block', textAlign: 'center',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingRight:'100px'}}>Save AND Proceed</h4>
                        </Link>
                        <Link to="/LibraryLayout">  
                            <h4 style={{display: 'inline-block', textAlign: 'right',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingLeft:'680px'}}>Decline</h4>
                        </Link>
                    </div>
            </div>
            </div>
        )
    }

}
