import React, { Component }  from 'react';
import Logo from '../img/Logo.png';
import {Link} from "react-router-dom";


class ChatBotConfiguration extends Component{
    
    render(){
        return(
            <div>
                <div className="logoImg">
                <img src={Logo} alt="logo" style={{height: '150px', display: 'inline-block',paddingRight:"45rem"}}/>
                <div  style={{display: 'inline-block', textAlign: 'right',fontSize: '30px', fontWeight:'bold', fontFamily:'Helvetica',left:"40%"}}>ChatBot Configuration</div>
            </div>

                <div className="video"
                    style={{
                        position: "relative",
                        paddingBottom: "56.25%" /* 16:9 */,
                        paddingTop: 50,
                        height: 0
                    }}>
                    <iframe
                        style={{
                        position: "absolute",
                        top: 40,
                        left: 0,
                        width: "100%",
                        height: "85%",
                        
                        }}
                        src={`http://52.172.146.59:8080/com.zensar.zenlabs.zeva/`}
                        title="configureInstance"
                        frameBorder="0"/>
                 </div>
                 <div className="tabView" style={{alignContent:'center'}}>
                    <Link to="/DashboardLayout">  
                        <h4 style={{display: 'inline-block', textAlign: 'center',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingRight:'100px'}}>Save AND Proceed</h4>
                    </Link>
                    <Link to="/LibraryLayout">  
                        <h4 style={{display: 'inline-block', textAlign: 'right',fontSize: '25px', fontWeight:'bold', fontFamily:'Helvetica', color:'#4303D5',paddingLeft:'680px'}}>Decline</h4>
                    </Link>
                </div>
            </div>
           
        )
    }

}
export default ChatBotConfiguration;